import React, { useState } from "react";
import { connect } from "react-redux";

import { withStyles } from "@mui/styles";
import {
  IconButton,
  List,
  ListItem,
  ListItemText,
  Popover,
} from "@mui/material";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';


const useStyles = (theme) => ({
  root: {
    textAlign: "center",
  },
  paper: {
    marginBottom: "20px",
  },
});

const DisplayEditAndDelete = (props) => {
  const { userId, postOrCommentUserId } = props;
  const [anchorEl, setAnchorEl] = useState(null);
  const [openListIcons, setOpenListIcons] = useState(false);

  const id = openListIcons ? "simple-popover" : undefined;

  const handleOpen = (event) => {
    setAnchorEl(event.currentTarget);
    setOpenListIcons(true);
  };

  const handleClose = () => {
    setOpenListIcons(false);
    setAnchorEl(null);
  };

  const handleOpenEditPost = () => {
    props.setEditPost(true);
    setOpenListIcons(false);
  };

  const handleOpenEditComment = () => {
    props.setEditComment(true);
    setOpenListIcons(false);
  };

 


  let display;

  if (props.type === "post" && userId === postOrCommentUserId) {
    display = (
      <IconButton aria-label="settings">
        <MoreVertIcon aria-describedby={id} onClick={handleOpen} />
        <Popover
          id={id}
          open={openListIcons}
          anchorEl={anchorEl}
          onClose={handleClose}
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "center",
          }}
        >
          <List component="nav" aria-label="main mailbox folders">
            <ListItem button onClick={handleOpenEditPost}>
              {/* <ListItemIcon>
                <FavoriteBorderIcon />
              </ListItemIcon> */}
              <ListItemText primary="Edit" />
            </ListItem>
            <ListItem button onClick={props.handleDeletePost}>
              {/* <ListItemIcon>
                <FavoriteBorderIcon />
              </ListItemIcon> */}
              <ListItemText primary="Delete" />
            </ListItem>
          </List>
        </Popover>
      </IconButton>
    );
  } else if (props.type === "comment" && props.postOrCommentUserId === userId) {
      display = (
        <IconButton aria-label="settings">
          <ExpandMoreIcon aria-describedby={id} onClick={handleOpen} />
          <Popover
            id={id}
            open={openListIcons}
            anchorEl={anchorEl}
            onClose={handleClose}
            anchorOrigin={{
              vertical: "bottom",
              horizontal: "center",
            }}
            transformOrigin={{
              vertical: "top",
              horizontal: "center",
            }}
          >
            <List component="nav" aria-label="main mailbox folders">
              <ListItem button onClick={handleOpenEditComment}>
                {/* <ListItemIcon>
                          <FavoriteBorderIcon />
                        </ListItemIcon> */}
                <ListItemText primary="Edit" />
              </ListItem>
              <ListItem button onClick={props.handleDeleteComment}>
                {/* <ListItemIcon>
                          <FavoriteBorderIcon />
                        </ListItemIcon> */}
                <ListItemText primary="Delete" />
              </ListItem>
            </List>
          </Popover>
        </IconButton>
      );
   
  } else {
    display = "";
  }
  return display;
};

const putReduxStateToProps = (reduxState) => ({ reduxState });
export default connect(putReduxStateToProps)(
  withStyles(useStyles)(DisplayEditAndDelete)
);
